import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Catcher from "../views/Catcher.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/catcher/:id",
    name: "Catcher",
    component: Catcher,
    props: true,
  },
  {
    path: "/about",
    name: "About",
    component: () => import("../views/About.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
